.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  margin-bottom: 50px;
}

.hamburger_menu {
  position: relative;
  cursor: pointer;
}

.list_container {
  position: absolute;
  background-color: white;
  border-radius: 5px;
  width: 80%;
  max-width: 500px;
  top: 100px;
  right: 50%;
  transform: translateX(50%);
  box-shadow: 0px 3px 15px rgba(0, 0, 0, 0.2);
}

hr {
  margin-bottom: 10px;
  color: hsl(207, 33%, 95%);
}

.list_container ul {
  text-align: center;
  padding: 20px;
}

.list_container ul li {
  list-style: none;
  padding-bottom: 10px;
  text-transform: uppercase;
  font-weight: bold;
  cursor: pointer;
}
.list_container ul li:last-child {
  color: hsl(231, 7%, 65%);
}

.navbar {
  width: 320px;
  margin-right: 100px;
  display: none;
}

.navbar ul {
  display: flex;
  justify-content: end;
  gap: 20px;
}
.navbar ul li {
  list-style: none;
  text-transform: uppercase;
  font-weight: bold;
  font-size: 10px;
  cursor: pointer;
}

.overlay {
  position: absolute;
  top: 0;
  right: 0;
  z-index: -1;
  border-bottom-left-radius: 50px 50px;
  background-color: hsl(207, 33%, 95%);
  height: 60%;
  width: 40%;
}

.navbar ul li:last-child {
  color: hsl(231, 7%, 65%);
}

@media screen and (min-width: 550px) {
  .hamburger_menu {
    display: none;
  }
  .list_container {
    display: none;
  }

  .navbar {
    display: block;
  }

  .logo {
    padding-left: 110px;
  }

  .overlay {
    width: 50%;
    height: 47%;
  }
}
