@import url("https://fonts.googleapis.com/css2?family=Barlow&family=Barlow+Condensed:wght@400;700&display=swap");

.image_container {
  max-width: 800px;

  overflow: hidden;
}

.image_container img {
  width: 100%;
  margin-left: 20%;
}

.text_container {
  padding: 40px;
}

.text_container p {
  display: flex;
  align-items: center;
  text-transform: uppercase;
  color: hsl(231, 7%, 65%);
  font-size: 12px;
  letter-spacing: 4px;
  margin-bottom: 20px;
}

.new {
  background-color: hsl(230, 29%, 20%);
  padding: 5px 10px;
  border-radius: 20px;
  color: white;
  text-transform: uppercase;
  font-weight: bold;
  margin-right: 10px;
}

.text_container h1 {
  color: hsl(230, 29%, 20%);
  text-transform: uppercase;
  font-family: "Barlow Condensed", sans-serif;
  margin-bottom: 20px;
}

.project {
  color: hsl(231, 7%, 65%);
  margin-bottom: 20px;
}
.text_container button {
  padding: 10px;
  border: none;
  border-radius: 4px;
  background-color: hsl(0, 100%, 68%);
  text-transform: uppercase;
  color: white;
  font-family: "Barlow", sans-serif;
  font-weight: bold;
  font-size: 10px;
  margin-right: 20px;
}

.preview {
  text-transform: uppercase;
  color: hsl(231, 7%, 65%);
  letter-spacing: 2px;
  font-size: 10px;
}

@media screen and (min-width: 550px) {
  .main {
    display: flex;
    flex-direction: row-reverse;
    align-items: center;
    padding-left: 100px;
  }
}
